import { Form } from "./admin/columns/form";
import { EventConstants } from "./constants";
import { initAcServices } from "./helpers/admin-columns";
import $ from 'jquery';
import ColumnConfigurator from "./admin/columns/column-configurator";
import Modal from "./modules/modal";
import Feedback from "./admin/columns/feedback";
import InfoScreenOption from "./admin/columns/screen-options";
import { initAcTooltips } from "./plugin/tooltip";
import { initPointers } from "./modules/ac-pointer";
import { initUninitializedListScreens } from "./admin/columns/listscreen-initialize";
import 'nodelist-foreach-polyfill';
import Modals from "./modules/modals";
var AcServices = initAcServices();
AcServices.registerService('Modals', new Modals());
new ColumnConfigurator(AcServices);
document.addEventListener('DOMContentLoaded', function () {
    initSaveHandlers();
    // Init the form
    document.querySelectorAll('#listscreen_settings').forEach(function (formElement) {
        AcServices.registerService('Form', new Form(formElement, AcServices));
    });
    // Init the Pro promotion Modal
    document.querySelectorAll('#ac-modal-pro').forEach(function (proModal) {
        AcServices.getService('Modals').register(new Modal(proModal), 'pro');
    });
    document.querySelectorAll('#ac_list_screen').forEach(function (select) {
        select.addEventListener('change', function () {
            document.querySelectorAll('.view-link').forEach(function (link) { return link.style.display = 'none'; });
            select.closest('form').submit();
            select.disabled = true;
            select.nextElementSibling.style.display = 'inline-block';
        });
    });
    document.querySelectorAll('#direct-feedback').forEach(function (feedbackElement) { return new Feedback(feedbackElement); });
    if (AC.hasOwnProperty('uninitialized_list_screens')) {
        initUninitializedListScreens(AC.uninitialized_list_screens);
    }
    // Screen Options
    document.querySelectorAll('[data-ac-screen-option="show_column_id"] input').forEach(function (el) { return new InfoScreenOption(el, 'show-column-id', document.querySelector('.ac-boxes')); });
    document.querySelectorAll('[data-ac-screen-option="show_column_type"] input').forEach(function (el) { return new InfoScreenOption(el, 'show-column-type', document.querySelector('.ac-boxes')); });
    document.querySelectorAll('[data-ac-screen-option="show_list_screen_id"] input').forEach(function (el) { return new InfoScreenOption(el, 'show-list-screen-id', document.querySelector('.ac-admin')); });
    document.querySelectorAll('[data-ac-screen-option="show_list_screen_type"] input').forEach(function (el) { return new InfoScreenOption(el, 'show-list-screen-type', document.querySelector('.ac-admin')); });
});
AcServices.addListener(EventConstants.SETTINGS.FORM.LOADED, function (form) {
    document.querySelectorAll('.add_column').forEach(function (el) { return el.addEventListener('click', function () { return form.createNewColumn(); }); });
    document.querySelectorAll('a[data-clear-columns]').forEach(function (el) { return el.addEventListener('click', function () { return form.resetColumns(); }); });
    // Make column settings sortable
    var $form = $(form.getElement());
    $form.hasClass('ui-sortable')
        ? $form.sortable('refresh')
        : $form.sortable({ items: '.ac-column', handle: '.column_sort' });
});
AcServices.addListener(EventConstants.SETTINGS.FORM.SAVING, function () {
    document.querySelector('#cpac .ac-admin').classList.add('saving');
});
AcServices.addListener(EventConstants.SETTINGS.FORM.SAVED, function () {
    document.querySelector('#cpac .ac-admin').classList.remove('saving');
    document.querySelector('#cpac .ac-admin').classList.add('stored');
});
AcServices.addListener(EventConstants.SETTINGS.COLUMN.INIT, function (column) {
    initAcTooltips();
    initPointers(column.getElement().querySelectorAll('.ac-pointer'));
});
var initSaveHandlers = function () {
    var elements = document.querySelectorAll('.sidebox a.submit, .column-footer a.submit');
    AcServices.addListener(EventConstants.SETTINGS.FORM.READY, function (form) {
        elements.forEach(function (el) {
            el.addEventListener('click', function (e) {
                e.preventDefault();
                elements.forEach(function (el) { return el.setAttribute('disabled', 'disabled'); });
                form.submitForm();
            });
        });
    });
    AcServices.addListener(EventConstants.SETTINGS.FORM.SAVED, function () { return elements.forEach(function (el) { return el.removeAttribute('disabled'); }); });
};
